import React, { Component } from "react";
import "../App.css";
import girl from "../img/girl-photo.png";
export class SecondPage extends Component {
    render() {
        return (
            <div>
                <section id="second-page">
                    <div className="grid">
                        <div className="left-wrapper">
                            <div className="lines">
                                <span></span>
                                <span></span>
                                <span></span>
                                <span></span>
                            </div>

                            <h1>About Me</h1>

                            <p>
                                Lorem ipsum dolor sit amet consectetur
                                adipisicing elit. Ipsa vitae dignissimos fugit,
                                veniam accusamus saepe. Velit, amet in.
                                Temporibus, obcaecati alias libero optio nostrum
                                quo numquam laborum in eaque natus.
                            </p>
                        </div>
                        <div className="right-wrapper">
                            <img src={girl} alt="" />
                        </div>
                    </div>
                </section>
            </div>
        );
    }
}

export default SecondPage;
